body {
  background-color: #80a06e;
  margin: 0;
  font-style: normal;
  font-weight: 400;
}

.header {
  background-color: #637b56;
}

@keyframes huvudtext {
  0% {
    color: #637b56;
    top: 0;
    left: 0;
  }

  30% {
    color: #637b56;
    top: 0;
    left: 0;
  }

  60% {
    color: #000;
    top: 0;
    left: 0;
  }

  100% {
    color: #fff;
    top: 0;
    left: 0;
  }
}

.headertext {
  color: #fff;
  box-sizing: border-box;
  text-align: center;
  width: 100%;
  height: 100%;
  margin-top: 0;
  padding: 30px;
  font-size: 40px;
  animation-name: huvudtext;
  animation-duration: 4s;
  position: relative;
}

#addProjectBtn {
  color: #fff;
  background-color: #000;
  border-radius: 20px;
  height: 40px;
}

#removeProjectBtn {
  color: #fff;
  background-color: #000;
  border: none;
  border-radius: 5px;
  margin-right: 15px;
  font-size: 20px;
}

#searchField {
  border-radius: 20px;
  width: 85%;
  height: 40px;
}

.addProjectForm {
  justify-content: center;
  align-items: center;
  display: flex;
}

.project-card {
  border-radius: 10px;
  justify-content: center;
  align-items: center;
  width: 31%;
  height: 200px;
  margin: 20px 1% 10px;
  transition: transform .3s ease-in-out;
  display: flex;
}

.project-card:hover {
  transform: scale(1.05);
}

.cardDiv {
  white-space: wrap;
  flex-wrap: wrap;
  align-items: center;
  margin-left: 29px;
  margin-right: 29px;
  display: flex;
}

.scrum-board {
  justify-content: space-between;
  margin-top: 20px;
  display: flex;
}

.addAssignmentDiv {
  justify-content: center;
  display: flex;
}

.categoryColorDiv {
  justify-content: space-around;
  margin-left: 20%;
  margin-right: 30%;
  display: flex;
}

#devBackendColor, .dev-backend {
  color: #fff;
  background-color: purple;
  border-radius: 10px;
  padding: 5px;
}

#devFrontendColor, .dev-frontend {
  color: #fff;
  background-color: #87ceeb;
  border-radius: 10px;
  padding: 5px;
}

