  border-radius: 10px;
  padding: 5px;
}

.projectDetailsInput {
  border-radius: 20px;
  width: 250px;
  height: 40px;
}

.projectDetailsBtn {
  color: #fff;
  background-color: #000;
  border-radius: 20px;
  height: 40px;
}

.column {
  background-color: #fff;
  border-radius: 5px;
  flex: 1;
  margin: 0 10px;
  padding: 10px;
}

.assignment-card {
  border-radius: 5px;
  margin: 10px 0;
  padding: 10px;
}

a:link, aside a:link {
  color: #000;
  text-decoration: none;
}

#removeProjectBtn:hover {
  color: #fff;
  background-color: #000;
  border-radius: 5px;
